  font-size: 23px;
}
.page-titles .breadcrumb {
  padding: 0px;
  margin: 0px;
  background: transparent;
  font-size: 12px;
}
.page-titles .breadcrumb li a {
  color: #fff;
}
.page-titles .breadcrumb .breadcrumb-item + .breadcrumb-item::before {
  content: "\e649";
  font-family: themify;
  color: #ced4da;
  font-size: 11px;
}
.page-titles .breadcrumb .breadcrumb-item.active {
  color: #fb9678;
  font-weight: 500;
}

  
